export default function configWeather(objValues, today) {
    return {
        weather : {
            pointStyle: (ctx) => {
                if (
                    (ctx.index === new Date().getHours() && new Date().getDate() === new Date(today).getDate()) || 
                    objValues.indexOf(Math.max(...objValues)) === ctx.index ||
                    objValues.indexOf(Math.min(...objValues)) === ctx.index) return true
                else if (ctx.active) return true
                else return false
            },
            lineBackgroundColor: null,
            lineBorderColor: null,
            scales: {
                y: {
                    max: null,
                    min: null
                }
            },
            showAllTooltips: {
                display: true,
                max: true,
                min: true
            }
            
        },
        precipprob: {
            pointStyle: (ctx) => {
                if (
                    (ctx.index === new Date().getHours() && new Date().getDate() === new Date(today).getDate()) || 
                    objValues.indexOf(Math.max(...objValues)) === ctx.index) return true
                else return false
            },
            lineBorderColor: '#2ef8ff',
            lineBackgroundColor: 'rgba(46, 248, 255, 0.2)',
            scales: {
                y: {
                    max: 100,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: true,
                max: true,
                min: false
            } 
        },
        uvindex: {
            pointStyle: (ctx) => {
                if (
                    (ctx.index === new Date().getHours() && new Date().getDate() === new Date(today).getDate()) || 
                    objValues.indexOf(Math.max(...objValues)) === ctx.index) return true
                else if (ctx.active) return true
                else return false
            },
            lineBorderColor: 'rgb(49, 255, 46)',
            lineBackgroundColor: 'rgba(49, 255, 46, 0.5)',
            scales: {
                y: {
                    max: 11,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: true,
                max: true,
                min: false
            } 
        },
        wind: {
            pointStyle: null,
            lineBorderColor: 'rgb(49, 255, 196)',
            lineBackgroundColor: 'rgba(49, 255, 196, 0.5)',
            scales: {
                y: {
                    max: Math.max(...objValues) + 30,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: false,
                max: null,
                min: null
            } 
        },
        precipitation: {
            pointStyle: null,
            lineBackgroundColor: null, 
            lineBorderColor: null,
            scales: {
                y: {
                    max: 10,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: false,
                max: null,
                min: null
            }
        },
        feelslike: {
            pointStyle: (ctx) => {
                if (
                    (ctx.index === new Date().getHours() && new Date().getDate() === new Date(today).getDate()) || 
                    objValues.indexOf(Math.max(...objValues)) === ctx.index ||
                    objValues.indexOf(Math.min(...objValues)) === ctx.index) return true
                else if (ctx.active) return true
                else return false
            },
            lineBackgroundColor: null,
            lineBorderColor: null,
            scales: {
                y: {
                    max: null,
                    min: null
                }
            },
            showAllTooltips: {
                display: true,
                max: true,
                min: true
            } 
        },
        humidity: {
            pointStyle: null,
            lineBorderColor: 'rgb(137, 46, 255)',
            lineBackgroundColor: (ctx) => {
                const canvas = ctx.chart.ctx;
                const h = ctx.chart.chartArea.width
                const gradient = canvas.createLinearGradient(0, 0, 0, h);

                gradient.addColorStop(0, 'rgba(137, 46, 255, 0.5)');
                gradient.addColorStop(0.5, 'rgba(82, 191, 13, 0.5)');

                return gradient;
            },
            scales: {
                y: {
                    max: 100,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: false,
                max: null,
                min: null
            }
        },
        visibility: {
            pointStyle: null,
            lineBorderColor: '#fff',
            lineBackgroundColor: 'rgba(226, 227, 225, 0.5)',
            scales: {
                y: {
                    max: 45,
                    min: '0'
                }
            },
            showAllTooltips: {
                display: false,
                max: null,
                min: null
            }
        },
        pressure: {
            pointStyle: null,
            lineBorderColor: '#e866e2',
            lineBackgroundColor: 'rgba(232, 102, 226, 0.2)',
            scales: {
                y: {
                    max: Math.max(...objValues) + 50,
                    min: Math.min(...objValues) - 70
                }
            },
            showAllTooltips: {
                display: false,
                max: null,
                min: null
            }
        },
 
    }    
}