import { computed, onMounted, ref } from "vue"

export default function useGetMoonValues(dataForChart) {
    const rotateMoon = ref(-45)
    const counter = ref(0)
    const dateUnderScroll = new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1)

    const compDateUnderScroll = ref(dateUnderScroll)

    const getIllumination = computed(() => {
        return Math.round((dataForChart.value.moonphase > 0.5 ? Math.abs(dataForChart.value.moonphase - 1) / 0.5 : dataForChart.value.moonphase / 0.5) * 100)
    })

    const getNextMoon = computed(() => {
        return dataForChart.value.moonphase * 29.5 > 30 ? Math.abs(Math.round(dataForChart.value.moonphase * 29.5) - 30) + 15 : Math.abs(Math.round(dataForChart.value.moonphase * 29.5)) + 15 - 1
    })
        
    const getDateForMoon = new Date(new Date().getFullYear(), new Date().getMonth(), 0).getDate() + 
                            new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0).getDate() + 
                            new Date(new Date().getFullYear(), new Date().getMonth() + 2, 0).getDate()

                                     
    onMounted(() => {
        const elem = document.querySelector('.scroll-block')
        const width = elem.scrollWidth
        const oneHourDayOnTheLeft = (width - elem.clientWidth) / getDateForMoon / 12
        const count = (Date.now() - +new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1)) / 1000 /60 /60 / 2
        elem.scrollLeft = oneHourDayOnTheLeft * count
    })

    const scrollMoon = (e) => {
        const width = e.target.scrollWidth
        const leftWidth = e.target.scrollLeft
        const oneHourDayOnTheLeft = (width - e.target.clientWidth) / getDateForMoon / 12
        counter.value = leftWidth / width
        compDateUnderScroll.value = new Date(+dateUnderScroll + Math.round(2 * leftWidth /  oneHourDayOnTheLeft) * 60 *60 *1000)
    }

    return {
        rotateMoon, counter, dateUnderScroll, scrollMoon, getDateForMoon,
        getNextMoon, getIllumination, compDateUnderScroll
    }
}