import axios from "axios"
import { ref } from "vue"

export default function useFetchWeather(weather) {
    const weatherLoad = ref(false)
    const weatherProps = ref({})
    const weathers = ref([])
    const today = ref([])
    weathers.value = weather.days
    today.value = weathers.value[0]

    for (let i in weather) {
        if (i !== 'days') weatherProps.value[i] = weather[i]
    }

    weatherLoad.value = true
  
    return {
        weatherProps,
        weathers,
        weatherLoad,
        today
    }
}