import { ref } from "vue"
export default function useUVindex(today) {
    const uvIndex = [
        { name: 'low', value: 2 },
        { name: 'moderate', value: 5 },
        { name: 'high', value: 7 },
        { name: 'very High', value: 10 },
        { name: 'Extreme', value: 11 },
    ]

    const uvSubtitle = ref('low')

    uvIndex.find(item => item.value > today.uvindex ? uvSubtitle.value = item.name : null)

    return {
        uvIndex, uvSubtitle,
    }
}