import {ref} from 'vue' 
export default function useOCModal() {
    const selectedModal = ref(false)
    const titleModal = ref('')
    const icon = ref('') 
    const selectDate = ref('') 
    const yBody = ref(20)

    const openModal = (titleName, iconName, dateName) => {
        document.body.style.overflow = 'hidden'
        yBody.value = Math.abs(document.body.getBoundingClientRect().y)
        selectedModal.value = true
        titleModal.value = titleName
        icon.value = iconName            
        if (dateName) selectDate.value = dateName
    }

    const closeModal = () => {
        document.body.style.overflow = null
        yBody.value = 20
        selectedModal.value = false
        titleModal.value = ''
        icon.value = ''
        selectDate.value = ''
    }

    const changeOptions = (titleName, iconName) => {
        titleModal.value = titleName
        icon.value = iconName
    }

    const changeDate = date => selectDate.value = date
    
    return {
        openModal,
        closeModal,
        changeOptions,
        changeDate,
        selectedModal,
        titleModal,
        icon,
        selectDate,
        yBody
    }
}