import { computed, ref } from "vue"

export default function useWind(today) {
    const winddir = ref(today.winddir)

    const rumbs = [
        {deg: 0, name: 'N'},
        {deg: 22.5, name: 'NNW'},
        {deg: 45, name: 'NW'},
        {deg: 67.5, name: 'WNW'},
        {deg: 90, name: 'W'},
        {deg: 112.5, name: 'WSW'},
        {deg: 135, name: 'SW'},
        {deg: 157.5, name: 'SSW'},
        {deg: 180, name: 'S'},
        {deg: 202.5, name: 'SSE'},
        {deg: 225, name: 'SE'},
        {deg: 247.5, name: 'ESE'},
        {deg: 270, name: 'E'},
        {deg: 292.5, name: 'ENE'},
        {deg: 315, name: 'NE'},
        {deg: 337.5, name: 'NNE'},             
    ]
    
    const rumb = computed(() => {
        return rumbs.reduce((sum, cur) => {
            if (sum.deg > 360) sum.deg = sum.deg - 360
            if (sum.deg > cur.deg) {
                sum.name = cur.name
            }
            return sum
        }, {deg: 360 - winddir.value, name: 'N'})
    })

    return {
        winddir,
        rumb,
    }
}