<template>
    <Container>
        <VTitle :side="'left'">
            <template #icon="{style}">
                <i class="fa-solid fa-temperature-half" :style="style"></i>
            </template>
            <template #text>
                Feels like
            </template>
        </VTitle>
        <Wrapper class="feels">
            <div class="feels-like__value">{{ Math.round(today.feelslike) }}&deg;</div>
            <div class="feels-like__description">{{ description }}</div>
        </Wrapper>
    </Container>
</template>

<script setup>
import { computed } from 'vue';
const { today } = defineProps({
    today: {
        type: Object,
        required: true
    },
})

const descriptionFeels = [
    'The wind makes it seem colder',
    'Humidity makes it seem warmer',
    'Feels like actual temperature'
]


const description = computed(() => {
    if (today.feelslike < today.temp) return descriptionFeels[0]
    else if (today.feelslike > today.temp) return descriptionFeels[1]
    else return descriptionFeels[2]
})

</script>

<style  scoped>

.feels {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   flex: 1 0 0;
}
.feels-like__value {
    font-size: 2em;
    font-weight: normal;
}
.feels-like__description {
}
</style>