<template>
    <div class="modal" @click="selectBar ? selectBar = false : null">
        <div class="modal__title">
                <div class="title">
                    <i :class="`fa-${icon}`" class="fa-solid"></i>
                    {{ titleModal }}
                </div>
            <div class="close">
                <div class="close__modal" @click="() => emit('closeModal')">
                    <i class="fa-solid fa-xmark"></i>
                </div>
            </div>
        </div>
        <ModalMoon 
            v-if="titleModal === 'Moon'"
            :dataForChart="dataForChart"/>
        <div class="modal__sun" v-else-if="titleModal === 'Sun'"></div>
        <div class="modal__average" v-else-if="titleModal === 'Average values'"></div>
        <div class="modal__standart" v-else>
            <div class="modal__container">
                <div class="modal__calendar">
                    <CalendarElem 
                        v-for="(day) in weathers" 
                        :selectDate="selectDate" 
                        :today="today.datetime" 
                        :day="day.datetime" 
                        :key="day.datetime"
                        @click="changeDate(day.datetime)" 
                        />
                </div>
                <div class="calendar__fullname">
                        {{ fullnameCalendar }}
                </div>
                <Hr />
                <div class="modal__description">
                    <div class="modal__bar">
                        <div :style="{
                            backgroundColor: selectBar ? '#31302e': null,
                            color: selectBar ? '#4a484485': null,
                        }"  class="select" @click.stop="selectBar = !selectBar">
                            <i :class="`fa-${icon}`" class="fa-solid"></i>
                            <i class="fa-solid fa-chevron-down"></i>
                        </div>
                        <Transition name="bounce">
                            <div class="select__bar" v-if="selectBar">
                                <MyModalSelect 
                                    v-for="menu in selectMenu"
                                    :menu="menu"
                                    :icon="icon"
                                    :key="menu.id" 
                                    @click="changeOptions(menu.name, menu.icon, menu.id)"/>
                            </div>
                        </Transition>
                    </div>
                    <div class="modal__specification">
                        <div
                        v-for="chart in selectMenu[idMenu - 1]?.chart" 
                        :key="chart.id"
                        >    
                            <TitleBarChart
                            :dataForChart="dataForChart"
                            :chart="chart"
                            :icon="icon"
                            :hour="hour"/>
                            <BarChart
                            :chart="chart"
                            :today="dataForChart"
                            />
                        </div>
                        <div class="modal__menu-description">
                            <ModalMenuDescription 
                            v-for="menuDesc in selectMenu[idMenu - 1]?.menuDescription"
                            :title="menuDesc.title"
                            :body="menuDesc.body"
                            :key="menuDesc.id"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import BarChart from '@/components/modalView/BarChart.vue'
import CalendarElem from '@/components/modalView/CalendarElem.vue'
import MyModalSelect from '@/components/modalView/MyModalSelect.vue'
import TitleBarChart from '@/components/modalView/TitleBarChart.vue'
import ModalMoon from '@/components/modalView/ModalMoon.vue'
import ModalMenuDescription from '@/components/modalView/ModalMenuDescription.vue'
import {toRefs, ref, watch, computed, onMounted} from 'vue'
import Hr from '../UI/Hr.vue'
const props = defineProps({
    today: {
        type: Object,
        required: true
    },
    titleModal: {
        type: String,
        required: true
    },
    icon: {
        type: String,
        required: true
    },
    weathers: {
        type: Array,
        required: true,
    },
    selectDate: {
        type: String,
        required: true
    },
    hour: {
        type: Number
    }
})

const {today, titleModal, icon, weathers, selectDate, hour} = toRefs(props) 

const selectMenu = [
    {id: 1, name: 'Weather', icon: 'cloud', menuDescription: [
        {id: 1, title: 'Main', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'weather', prop: 'temp', unit: '°'},
        {id: 2, title: 'Chance of precipitation', configName: 'precipprob', prop: 'precipprob', unit: '%'},
    ]},
    {id: 2, name: 'UV-index', icon: 'sun', menuDescription: [
        {id: 1, title: 'Comparison by day', body: ''},    
        {id: 2, title: 'About the UV Index', body: 'The UV Index (UVI), developed by the World Health Organization, measures ultraviolet radiation. The higher the UV index, the greater the likelihood of harm to health and the faster it can occur. The UV index can help you decide when to protect yourself from the sun and when to stay away from the sun. WHO recommends staying in the shade, using sunscreen, hats and protective clothing at level 3 (moderate) and above.'},
    ], chart: [
        {id: 1, title: '', configName: 'uvindex', prop: 'uvindex', unit: ''},
    ]},
    {id: 3, name: 'Wind', icon: 'wind', menuDescription: [
        {id: 1, title: 'Main', body: ''},
        {id: 2, title: 'About speed and gusts of wind', body: ''},
        {id: 3, title: 'Beaufort scale', body: ''},
        {id: 4, title: 'About the beaufort scale', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'wind', prop: 'windspeed', unit: 'km/h'},
    ]},
    {id: 4, name: 'Precipitation', icon: 'droplet', menuDescription: [
        {id: 1, title: 'Main', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'precipitation', prop: 'precip', unit: 'mm', icon: false},
    ]},
    {id: 5, name: 'Feels like', icon: 'temperature-three-quarters', menuDescription: [
        {id: 1, title: 'Main', body: ''},
        {id: 2, title: 'About the "Feels like" temperature indicator', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'feelslike', prop: 'feelslike', unit: '°'},
    ]},
    {id: 6, name: 'Humidity', icon: 'water', menuDescription: [
        {id: 1, title: 'Main', body: ''},
        {id: 2, title: 'About relative humidity', body: ''},
        {id: 3, title: 'About dew point', body: ''}               
    ], chart: [
        {id: 1, title: '', configName: 'humidity', prop: 'humidity', unit: '%'},
    ]},
    {id: 7, name: 'Visibility', icon: 'eye', menuDescription: [
        {id: 1, title: 'Main', body: ''},
        {id: 2, title: 'About visibility', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'visibility', prop: 'visibility', unit: 'km'},
    ]},             
    {id: 8, name: 'Pressure', icon: 'gauge', menuDescription: [
        {id: 1, title: 'Main', body: ''},
        {id: 2, title: 'About pressure', body: ''},
    ], chart: [
        {id: 1, title: '', configName: 'pressure', prop: 'pressure', unit: 'hPa'},
    ]},
]    

const idMenu = computed(() => {
    return selectMenu.find(item => item.name === titleModal.value)?.id || 1
})

const fullnameCalendar = computed(() => {
    return new Date(selectDate.value).toLocaleString('en-Us', {
        weekday: "long",
        day: "numeric",
        month: "long",
        year: "numeric",
    }) + '.'
})

const selectBar = ref(false)

const dataForChart = computed(() => {
    return weathers.value[Math.abs(new Date(selectDate.value).getDate() - new Date().getDate())]
})

const emit = defineEmits()

const changeDate = (date, index) => {
    emit('changeDate', date)

}

const changeOptions = (name, icon, id) => {
    selectBar.value = false
    emit('changeOptions', name, icon);
}

const xSwipe = ref(null)
onMounted(() => {
    const modal = document.querySelector('.modal__description')
    modal.addEventListener('touchstart', e => xSwipe.value = e.changedTouches[0].clientX);
    modal.addEventListener('touchend', e => e.changedTouches[0].clientX - xSwipe.value < -50 && swipe('right'));
    modal.addEventListener('touchend', e => e.changedTouches[0].clientX - xSwipe.value > 50 && swipe('left'));
})

const swipe = (method) => {
    const dateSelected = new Date(selectDate.value)

    method === 'right' ? dateSelected.setDate(dateSelected.getDate() + 1) : dateSelected.setDate(dateSelected.getDate() - 1)

    weathers.value.forEach(item => {
        if (new Date(item.datetime).getDate() === dateSelected.getDate()) {
            const animation = method === 'right' ? 'animationLeft' : 'animationRight'

            const movedElem = document.querySelector('.modal__specification')
            setTimeout(() => {
                movedElem.classList.remove(animation)  
            }, 800)

            emit('changeDate', item.datetime)
            
            movedElem.classList.add(animation)
        }
    })
}
   
</script>

<style scoped>
.modal__title {
    position: sticky;
    top: 0px;
    background-color: rgba(27, 27, 29, 0.7);
    backdrop-filter: blur(15px);
    display: flex;
    height: fit-content;
    padding: 20px 0;
    z-index: 100;
}

.title {
    flex: 1 0 0;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    font-weight: bold;
    font-size: 1.2em;
}

.title i {
    font-size: 1.2em;
}

.close__modal {
    background-color: #39383d;
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    width: 2em;
    height: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.close {
    color: #c4c4c4;
}

.close i {
    font-size: 1.2em;
}

.modal__container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 5em;
}

.modal__calendar{
    display: flex;
    justify-content: space-between;
    text-align: center;
    gap: 20px;
    overflow-x: auto;
    scroll-snap-type: x proximity;
    scrollbar-width: none; /* firefox */
    padding: 0 20px;
}

.modal__calendar::-webkit-scrollbar { 
    width: 0;
    height: 0;
}

.calendar__fullname {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: normal;
    padding-bottom: 5px;
}

.modal__description {
    /* background: linear-gradient(1turn,#c4c4c4, #c4c4c4 50%, #39383d 80%, #1c1c1e 100%); */
    margin-top: 5px;
    position: relative;
}

.modal__bar {
    display: flex;
    flex-direction: column;
    align-items: self-end;
    padding: 15px 20px;
    position: absolute;
    top: 0;
    right: 0;
}

.select {
    transition: all 0.1s;
    background-color: #39383d;
    border-radius: 15px;
    cursor: pointer;
    display: flex;
    padding: 8px 0 8px 12px;
    align-items: center;
    width: max-content;
    position: relative;
}

.select i:last-child{
    padding: 0 8px;
    font-size: 0.8em;
}

.select__bar {
    transform-origin: right top;
    position: absolute;
    top: 90%;
    width: 50vw;
    height: max-content;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #39383d;
    z-index: 10;
}

.modal__specification {
    overflow: hidden;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.modal__menu-description {
   display: flex;
   flex-direction: column;
   gap: 20px;
}

.bounce-enter-active {
  animation: bounce-in 0.25s;
}
.bounce-leave-active {
  animation: bounce-in 0.25s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.animationRight {
    animation: swipeRight 0.75s;
}

.animationLeft {
    animation: swipeLeft 0.75s;
}



@keyframes swipeLeft {
    0% {
        transform: translateX(-100px);
        opacity: 0.5;
    }

    20% {
        opacity: 0.25;
    }

    40% {
        transform: translateX(-500px);
        opacity: 0;
    }

    60% {
        transform: translateX(500px);
        opacity: 0;
    }

    80% {
        transform: translateX(0px);
        opacity: 0.25;
    }

    100% {
        transform: translateX(0px);
        opacity: 1;
    }

}

@keyframes swipeRight {
    0% {
        transform: translateX(100px);
        opacity: 0.5;
    }

    20% {
        opacity: 0.25;
    }

    40% {
        transform: translateX(500px);
        opacity: 0;
    }

    60% {
        transform: translateX(-500px);
        opacity: 0;
    }

    80% {
        transform: translateX(0px);
        opacity: 0.25;
    }

    100% {
        transform: translateX(0px);
        opacity: 1;
    }
}

</style>