<template>
    <Container class="scroll-hour-menu">
        <VTitle>
            <template #icon="{}">
                <i class="fa-regular fa-clock" :style="{}"></i>
            </template>
            <template #text>
                <span
                class="text"
                :style="{opacity: opacity + 1, display: opacity < -1 ? 'none' : 'flex'}">{{ today.description }}</span>
                <span
                class="text" 
                :style="{opacity: -opacity - 1, display: opacity < -1 ? 'flex' : 'none'}"> Hourly forecast</span>
            </template>
        </VTitle>
        <Hr class="hr-scroll" />
        <Wrapper class="scroll-hour-menu-bar">
            <ScrollHourMenuBarItem 
            v-for="(hour, index) in concatHourTwoDays" 
            :key="hour.datetimeEpoch" 
            :index="index"
            :hour="hour"/>
        </Wrapper>
    </Container>
</template>

<script setup>
import useConcatHourTwoDays from '@/hooks/useConcatHourTwoDays'
import ScrollHourMenuBarItem from '@/components/basicView/ScrollHourMenuBarItem.vue'
    
const {opacity, today, tomorrow} = defineProps({
    today: {
        type: Object,
        required: true
    },
    tomorrow: {
        type: Object,
        required: true
    },
    opacity: Number
})

const { concatHourTwoDays } = useConcatHourTwoDays(today, tomorrow)
</script>

<style scoped>
section.scroll-hour-menu-bar {
    margin-left: 0;
    margin-right: 0;
    flex-direction: row;
    flex: none;
    justify-content: space-between;
    text-align: center;
    gap: 20px;
    font-weight: bolder;
    overflow-x: auto;
    scroll-snap-type: x proximity;
    scrollbar-width: none; /* firefox */
}

.scroll-hour-menu-bar::-webkit-scrollbar { /* webkit */
    width: 0;
    height: 0;
}

.text {
    font-weight: normal;
}

.hr-scroll {
    margin-left: 10px;
}

</style>