<template>
    <Container>
        <VTitle :side="'left'">
            <template #icon="{style}">
                <i class="fa-solid fa-arrow-trend-up" :style="style"></i>
            </template>
            <template #text>
                average
            </template>
        </VTitle>
        <Wrapper class="average-body">
            <div>
                <div class="average__value">{{ average }}&deg;</div>
                <div class="average__value-desc">differs from the average daily temperature maximum</div>
            </div>
            <div class="average__block-info">
                <div class="average__block-elem">
                    <div class="average__elem">Today</div>
                    <div class="average__elem">Max.: {{ Math.round(today.tempmax) }}&deg;</div>
                </div>
                <div class="average__block-elem">
                    <div class="average__elem">Average</div>
                    <div class="average__elem">Max.: {{ Math.round(today.tempmax / 2) }}&deg;</div>
                </div>
            </div>
        </Wrapper>
    </Container>
</template>

<script setup>
import { computed } from 'vue';

const { today } = defineProps({
    today: {
        type: Object,
        required: true
    }
})

const average = computed(() => {
    return Math.round((today.tempmax + today.tempmin) / 2)
})
</script>


<style scoped>

section.average-body {
    margin-bottom: 1rem;
}
.average__value {
    font-size: 2em;
    font-weight: normal;
}
.average__value-desc {
    font-weight: normal;
}
.average__block-info {
}
.average__block-elem {
    display: flex;
    justify-content: space-between;
}

.average__block-elem > div:last-child {
    font-weight: normal;
}

.average__block-elem > div:first-child {
    color: #d3cccc85;
}
.average__elem {
}

</style>