<template>
    <div 
        :class="{
            weatherIcon: chart.configName === 'weather',
            uvindexIcon: chart.configName === 'uvindex',
            windIcon: chart.configName === 'wind',
            humidityIcon: chart.configName === 'humidity',
            visibilityIcon: chart.configName === 'visibility',
            pressureIcon: chart.configName === 'pressure'
        }" class="icon">
        <div 
        v-if="chart.configName === 'weather' ||
              chart.configName === 'precipitation' ||
              chart.configName === 'feelslike'">
                  <NavbarWeather 
                    class="img__wrapper-navbar-weather"
                    v-for="(hour, index) in today.hours"
                    v-show="index % 2 !== 0" 
                    :key="hour"
                    :hour="hour"  />
        </div>
        <div v-else-if="chart.configName === 'uvindex'">
            <NavbarUVindex 
            class="img__wrapper-navbar-uvindex"   
            v-for="(count, index) in 12"
            :key="index"
            :count="count"
            :values="values"/>
        </div>
        <div v-else-if="chart.configName === 'wind'">
            <NavbarWind 
            class="img__wrapper-navbar-wind"          
            v-for="(dir, index) in today.hours"
            v-show="index % 2 !== 0" 
            :key="dir.id"
            :dir="dir"/>
        </div>
        <div v-else-if="chart.configName === 'humidity'">
            <NavbarHumidity 
            class="img__wrapper-navbar-humidity"
            v-for="(count, index) in 4"
            :key="index"
            :values="values"
            :count="count"/>
        </div>
        <div v-else-if="chart.configName === 'visibility'">
            <NavbarVisibility 
            class="img__wrapper-navbar-visibility"
            v-for="(count, index) in 12"
            :key="index"
            :values="values"
            :count="count"/>
        </div>
    </div>
</template>


<script setup>
  import NavbarPressure from '@/components/modalView/topNavbar/NavbarPressure'
  import NavbarHumidity from '@/components/modalView/topNavbar/NavbarHumidity.vue'
  import NavbarVisibility from '@/components/modalView/topNavbar/NavbarVisibility.vue'
  import NavbarUVindex from '@/components/modalView/topNavbar/NavbarUVindex.vue'
  import NavbarWind from '@/components/modalView/topNavbar/NavbarWind.vue'
  import NavbarWeather from '@/components/modalView/topNavbar/NavbarWeather.vue'

  const {values, chart, today} = defineProps({
    values: {
        type: Array,
        reuqired: true
    },
    chart: {
        type: Object,
        reqiured: true
    },
    today: {
        type: Object,
        reuqired: true
    }
  })
</script>

<style scoped>
.icon {
    position: absolute;
    width: 85%;
    padding-left: 10px;
}

.icon > div {
    display: flex;
}
.humidityIcon > div,
.pressureIcon > div,
.visibilityIcon > div,
.windIcon > div,
.uvindexIcon > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>