import { onBeforeMount, ref, watch} from "vue"


export default function useGetChartValues(today, chart) {
    const values = ref([])
    const datetime = ref('')
    const configName = ref('')

    const setValues = () => {
        values.value = today.value.hours.map(item => item[chart.value.prop])
        datetime.value = today.value.datetime
        configName.value = chart.value.configName
    }

    onBeforeMount(setValues)
    watch(today, setValues)
    watch(chart, setValues)

    return {
        values, configName, datetime,
        setValues
    }
}