<template>
    <div class="calendar__elem">
        <div class="calendar__name">{{ new Date(day).toLocaleString('en-Us', {weekday: "short"}).slice(0,1) }}</div>
        <button
            :class="{
                btnToday: day === today,
                btnActive: selectDate === day && day !== today,
                btnTodayActive: selectDate === day && day === today,
            }"
            class="calendar__number btn">
                {{ new Date(day).getDate() }}
        </button>
    </div>
</template>

<script setup>
const {day, today, selectDate} = defineProps({
    day: {
        type: String,
        required: true
    },
    today: {
        type: String,
        required: true
    },
    selectDate: {
        type: String,
    }
})

</script>

<style scoped>
.calendar__elem {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 5px 0;
}

.calendar__name {
    font-weight: bold;
    font-size: 0.7em;
}
.btn {
    color: #fff;
    border-radius: 50%;
    border: none;
    width: 2em;
    height: 2em;
    font-weight: normal;
    background: transparent;
    font-size: 1.1em;
}

.btnActive {
    color: #1a1a1c;
    background-color: #fff;
}

.btnToday {
    color: cyan;
}

.btnTodayActive {
    color: #1a1a1c;
    background-color: cyan;
}





</style>