<template>
    <Container>
        <VTitle>
            <template #icon="{style}">
                <i class="fa-solid fa-wind" :style="style"></i> 
            </template>
            <template #text>
                wind
            </template>
        </VTitle>
        <Wrapper class="wind">
            <div class="wind__block">
                <div class="wind__elem">
                    <div class="wind__value">{{ Math.round(today.windspeed) }}</div>
                    <div class="wind__description">
                        <div class="wind__description-prop">km/h</div>
                        <div class="wind__description-mark">Winds speed</div>
                    </div>
                </div>
                <Hr />
                <div class="wind__elem">
                    <div class="wind__value">{{ Math.round(today.windgust) }}</div>
                    <div class="wind__description">
                        <div class="wind__description-prop">km/h</div>
                        <div class="wind__description-mark">Wind gust</div>
                    </div>
                </div>
            </div>
            <div class="wind__compass">
                <div class="compass">
                    <img :src="require(`@/asserts/img/compass.png`)" alt="compass">
                    <div class="compass__wrapper" :style="{
                        transform: `rotate(${-winddir - 180}deg)`
                    }">
                        <i class="fa-solid fa-arrow-up"></i>
                    </div>
                    <div class="compass__circle">
                        <div class="circle__bg">
                            {{ rumb.name }}
                        </div>
                    </div>
                </div>
            </div>
        </Wrapper>
    </Container>
</template>

<script setup>
import useWind from '@/hooks/useWind'

const {today} = defineProps({
    today: {
        type: Object,
        required: true
    }
})

const {winddir, rumb} = useWind(today)

</script>

<style scoped>
section.wind {
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.wind__block {
    display: flex;
    flex-direction: column;
    flex-basis: 60%;
}
.wind__elem {
    display: flex;
    align-items: center;
    gap: 10px;
}
.wind__value {
    font-size: 3em;
    font-weight: bold;
    flex-basis: 25%;
    min-width: fit-content;
    text-align: right;
}
.wind__description {
    font-weight: normal;
}
.wind__description-prop {
    font-weight: normal;
    color: #d3cccc85
}
.wind__description-mark {
    font-weight: normal;
}
.wind__compass {
    flex-basis: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.compass {
    width: 9em;
    height: 100%;
    position: relative;
}

.compass img {
    width: 100%;
    height: auto;
    opacity: 0.5;
}

.compass__wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
}
.compass__wrapper i{
    align-self: stretch;
    position: relative;
    z-index: 3;
}

.compass__wrapper i::after {
    content: '';
    border-left: 0.13em solid white;
    display: flex;
    height: calc(100% - 3.5em);
    overflow: hidden;
    position: relative;
    width: 0.5px;
    top: 1.1em;
    left: 50%;
    transform: translateX(-45%);
    z-index: 1;
}

.compass__wrapper i::before {
    position: relative;
    top: 1.2em;
    left: 0;
    z-index: 2;
}
.compass__circle {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -55%);
    height: 100%;
    width: 100%;
    max-width: 3em;
    max-height: 3em;
    border-radius: 50%;
    font-weight: normal;
    text-align: center;
    transform-origin: center;
    background: linear-gradient(90deg ,#3774c1 calc(0% - 20px), #2c62bd calc(100% + 20px));
    z-index: 5;
}

.circle__bg {
    background-color: rgba(0, 0, 0, 0.02);
    flex: 1 0 0;
    align-self: stretch;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;

}

</style>