<template>
    <div class="ten-day-forecast-main__item">
        <Hr />
        <div class="ten-day-forecast-main__elem">
            <div class="left">
                <div class="ten-day-forecast-main__name">
                    {{new Date(day.datetime).getDate() === date ? 'Today' : new Date(day.datetime).toLocaleString('en-Us', {weekday: "short"})}}
                </div>
                <div class="ten-day-forecast-main__icon">
                    <img :src="require(`@/asserts/img/WeatherIcons-main/${day.icon}.png`)" :alt="day.icon">
                </div>
            </div>
            <div class="right">
                <div class="ten-day-forecast-main__min-temp ten-day-forecast-main__temp">{{ Math.round(day.tempmin)}}&deg;</div>
                <div class="ten-day-forecast-main__bar">
                    <div class="bar">
                        <div class="bar__line" :style="{left: leftTranslate + '%', width: lineWidth + '%'}"></div>
                        <div class="bar__circle" v-if="new Date(day.datetime).getDate() === date"></div>
                    </div>
                </div>
                <div class="ten-day-forecast-main__max-temp ten-day-forecast-main__temp">{{ Math.round(day.tempmax )}}&deg;</div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { onMounted, ref } from 'vue';

const {day, weathers} = defineProps({
    day: {
        type: Object,
        required: true
    },
    weathers: {
        type: Object,
        required: true
    },
})

const allTemps = Array.from(new Set(weathers.reduce((arr, day) =>  [...arr, Math.round(day.tempmin), Math.round(day.tempmax)], []).sort((a,b) => a - b)))
const allSteps = allTemps.length - 1
const leftTranslate = 100 - ((allSteps - allTemps.indexOf(Math.round(day.tempmin))) * 100 / allSteps)
const lineWidth = 100 - (((allSteps - allTemps.indexOf(Math.round(day.tempmax))) * 100 / allSteps) + leftTranslate)

const date = ref(new Date().getDate())

</script>

<style scoped>
.ten-day-forecast-main__elem {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 4.5px;
}

.left {
    flex: 0.6 0 0;
    display: flex;
    align-items: center;
    gap: 20px;
}

.right {
    flex: 1 0 0;
    display: flex;
    align-items: center;
}
.ten-day-forecast-main__name {
    flex: 1 0 0;
    font-weight: bold;
    font-size: 1.2em;
}
.ten-day-forecast-main__icon {
    width: 2.2em;
}

.ten-day-forecast-main__icon img {
    width: 100%;
    height: auto;
    color: #fff
}
.ten-day-forecast-main__min-temp {
    flex-basis: 10%;
   display: flex;
   justify-content: center;
   color: #d3cccc85;
}
.ten-day-forecast-main__temp {
   flex-basis: 10%;
   display: flex;
   justify-content: center;
   font-weight: normal;
}
.ten-day-forecast-main__bar {
    flex: 1 0 0;
}
.bar {
    position: relative;
    min-height: 4px;
    height: 5px;
    border-radius: 5px;
    border: none;
    background: rgba(8, 4, 35, 0.301);
    margin: 0 5px;
}

.bar__line {

    width: 50%;
    height: 100%;
    border-radius: 5px;
    background-color: #3aeca2a1;
    position: absolute;
}

.bar__circle {
    min-width: 4px;
    min-height: 4px;
    width: 7px;
    height: 7px;
    border-radius: 100%;
    background-color: white;
    border: 2px solid rgb(67, 67, 74);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 30%;
}

.ten-day-forecast-main__max-temp {
}
</style>