<template>
    <div class="bars">     
        <BarNavbar 
            class="top__navbar"
            :chart="chart"
            :today="today"
            :values="values"
             />
        <Line
        v-if="chartData.datasets[0].data.length"
        id="my-chart-id"
        :options="chartOptions"
        :data="chartData"
        :plugins="plugins"
        class="chart"
        :style="{paddingTop: chart.configName !== 'precipprob'? '20px' : '0px'}"
        />
    </div>
  </template>
  
  <script setup>

  import { Chart, Line } from 'vue-chartjs'
  import { 
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler } from 'chart.js'
  
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler)
  import configWeather from '@/components/configChart/configWeather'
  import {computed,  ref, toRefs} from 'vue'
  import useGetChartValues from '@/hooks/useGetChartValues'
  import BarNavbar from '@/components/modalView/topNavbar/BarNavbar.vue'

    const props = defineProps({
        today: {
            type: Object,
            required: true
        },
        chart: {
            type: Object
        }
    })

    
    const {today, chart} = toRefs(props)
        const {values, setValues, configName, datetime} = useGetChartValues(today, chart)

        const chartData = computed(() => ({
            labels: [...Array(24).keys()].map(i => i < 10 ? '0' + i : i),
            datasets: [
                {   
                    label: 'Data One',
                    data: values.value,
                    tension: 0.5,
                    elements: {
                        point: {
                            pointStyle: configWeather(values.value, datetime.value)[configName.value].pointStyle || 
                            ((ctx) => { 
                                        if (
                                            ctx.index === new Date().getHours() && 
                                            new Date().getDate() === new Date(datetime.value).getDate() || 
                                            ctx.active) return true
                                            else return false
                                        }),
                                        
                            backgroundColor: (ctx)=> {
                                if (values.value.indexOf(Math.max(...values.value)) === ctx.index ||
                                values.value.indexOf(Math.min(...values.value)) === ctx.index) return 'transparent'
                                else return '#fff'
                            },
                            radius: (ctx) => {
                                const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                                const size = Math.round(avgSize / 64);
                                return size > 8 ?  8 : size
                            },
                            hitRadis: (ctx) => {
                                const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                                const size = Math.round(avgSize / 64);
                                console.log(size)
                                return size > 8 ?  8 : size
                            },
                            borderWidth: (ctx) => {
                                const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                                const size = Math.round(avgSize / 64);
                                return size > 8 ?  8 : size
                            },
                            hoverRadius: (ctx) => {
                                const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                                const size = Math.round(avgSize / 64);
                                return size > 8 ?  8 : size
                            },
                            hoverBorderWidth: (ctx) => {
                                const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                                const size = Math.round(avgSize / 128);
                                return size > 8 ?  8 : size
                            },
                            borderColor: '#1c1c1e',
                        },
                        line: {
                            borderWidth: 5,
                            borderColor: configWeather(values.value, datetime.value)[configName.value].lineBorderColor || "#4060bf",
                            backgroundColor: configWeather(values.value, datetime.value)[configName.value].lineBackgroundColor || 
                                            ((ctx) => {
                                                const canvas = ctx.chart.ctx;
                                                const gradient = canvas.createLinearGradient(0, 0, 1000, 1000);

                                                gradient.addColorStop(0, 'rgb(19, 61, 66, 0.5)');
                                                gradient.addColorStop((new Date().getHours()) / 24, 'rgb(48, 99, 252, 0.5)');
                                                gradient.addColorStop(1, 'rgb(48, 99, 252, 0.5)');

                                                return gradient;
                                            })
                        }
                    },
                    
                    segment: {
                        borderDash: ctx => {
                            if (ctx.p1DataIndex <= new Date().getHours() && new Date().getDate() === new Date(datetime.value).getDate()) return [15,10]
                        },
                        borderDashOffset: 10
                    },
                    fill: 'start'
                },
                
            ]
        }))
        const chartOptions = computed(() => ({
            responsive: true,
            maintainAspectRatio: true,
            showAllTooltips: {
                display: configWeather(values.value, datetime.value)[configName.value].showAllTooltips.display,
                max: configWeather(values.value, datetime.value)[configName.value].showAllTooltips.max,
                min: configWeather(values.value, datetime.value)[configName.value].showAllTooltips.min,
            },
            plugins: {
                chartAreaBorder: {
                    borderColor: '#39383d',
                    borderWidth: 1,
                },
                verticalLiner: {
                    color: '#fff',
                    width: 3
                },
                tooltip: {
                    titleColor: '#b0b0b0',
                    titleAlign: 'center',
                    titleFont: (ctx) => {
                        const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                        const size = Math.round(avgSize / 16);
                        
                        return {
                            size: size,
                            weight: 500
                        };
                    },
                    bodyFont: (ctx) => {
                        const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                            const size = Math.round(avgSize / 16);
                            
                            return {
                                size: size,
                                weight: 500
                            };
                    },
                    bodyAlign: 'center',
                    displayColors: false,
                    callbacks: {
                        title: (ctx) => ctx[0].label + ':00',
                        
                        label: (ctx) => {
                            if (configName.value === 'visibility' && ctx.raw < 1) return ctx.raw * 1000 + ' '  + 'm'
                            return ctx.formattedValue + ''  + chart.value.unit
                        }
                    }
                }
            },
            scales: {
                x: {
                    color: '#b0b0b0',
                    type: 'category',
                    
                    ticks: {
                        maxTicksLimit: 4,
                        stepSize: 6,
                        font: (ctx) => {
                            const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                            const size = Math.round(avgSize / 24);
                            
                            return {
                                size: size,
                                weight: 500
                            };
                        },  
                        color: '#b0b0b0',
                    },
                    grid: {
                        color: '#39383d',
                    },
                    border: {
                        dash: [2,4],
                    }, 
                },  
                y: {
                    position: 'right',
                    max: configWeather(values.value, datetime.value)[configName.value].scales.y.max || Math.round(Math.max(...values.value) + 9),
                    min: +(configWeather(values.value, datetime.value)[configName.value].scales.y.min || Math.round(Math.min(...values.value) - 6)),
                    ticks: {
                        callback: (v) => v +  " " + chart.value.unit ,
                        color: '#b0b0b0',
                        font: (ctx) => {
                            const avgSize = Math.round((ctx.chart.height + ctx.chart.width) / 2);
                            const size = Math.round(avgSize / 24);

                            return {
                                size: size,
                                weight: 500
                            };
                        },     
                    },
                    grid: {
                        color: '#39383d',
                    }
                },           
            },          
        }))
        const plugins = ref([{
            id: 'verticalLiner',
            afterInit: (chart, args, opts) => {
                chart.verticalLiner = {}
            },
            afterEvent: (chart, args, options) => {
                const {inChartArea} = args
                chart.verticalLiner = {draw: inChartArea}
            },
            beforeTooltipDraw: (chart, args, options) => {
                const {draw} = chart.verticalLiner
                if (!draw) return
                const {ctx} = chart
                const {top, bottom} = chart.chartArea
                const {tooltip} = args
                const x = tooltip?.caretX
                if (!x) return
                ctx.save()
                ctx.strokeStyle = options.color
                ctx.lineWidth = options.width
                ctx.beginPath()
                ctx.moveTo(x, top)
                ctx.lineTo(x, bottom)
                ctx.stroke()
            }
            },
            {
                id: 'alwaysShowTooltip',
                afterDraw(chart, args, options) {
                    const {ctx} = chart;
                    const chartW = chart.chartArea.width
                    const chartH = chart.chartArea.height

                    ctx.save();
                    if (!chart.config._config.options.showAllTooltips.display) return
                    chart.data.datasets.forEach((dataset, i) => {
                        chart.getDatasetMeta(i).data.forEach((datapoint, index) => {
                            const {x, y} = datapoint.tooltipPosition();
                            const correctCord = {xC: 0, yC: 10}
                            if (x + 10 > chartW) correctCord.xC = 30
                            if (y - 20 < 0) correctCord.yC = -20

                            ctx.fillStyle = '#b0b0b0'
                            ctx.font = 'bolder 14px Arial'
                            if (values.value.indexOf(Math.min(...values.value)) === datapoint.$context.dataIndex && chart.config._config.options.showAllTooltips.min) {
                                ctx.fillText('Min', x - correctCord.xC, y - correctCord.yC)
                            }
                            else if (values.value.indexOf(Math.max(...values.value)) === datapoint.$context.dataIndex && chart.config._config.options.showAllTooltips.max) {                                
                                ctx.fillText('Max', x - correctCord.xC, y - correctCord.yC)

                            }
                        })
                    })
                }
            },
            {id: 'chartAreaBorder',
            beforeDraw(chart, args, options) {
                const {ctx, chartArea: {left, top, width, height}} = chart;
                ctx.save();
                ctx.strokeStyle = options.borderColor;
                ctx.lineWidth = options.borderWidth;
                ctx.setLineDash(options.borderDash || []);
                ctx.lineDashOffset = options.borderDashOffset;
                ctx.strokeRect(left, top, width, height);
                ctx.restore();
            },
            }
            ])

  </script>

<style scoped>

.bars {
    border: 1px solid #39383d;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
</style>