<template>
    <div class="modal__moon">
            <div class="moon__block">
                <div class="prop" :style="{
                    '--scroll': `${counter * -1}s`,
                }"></div>
                <img :src="require(`@/asserts/img/moon.png`)" class="moonIMG" alt="moon" :style="{
                    '--scroll': `${counter * -1}s`
                }">
            </div>
            <div class="moon__scroll">
                <div class="scroll__block-day">
                        {{ compDateUnderScroll.toLocaleString('en-US', {day: 'numeric', month: 'long', hour: 'numeric', minute: 'numeric', hourCycle: 'h24'}) }}
                    </div>
                <div class="scroll-block" @scroll="scrollMoon">
                    
                    <div class="scroll__zabor" v-for="i in getDateForMoon" style="{display: flex;}" :key="i">
                        <div  class="zabor">|||||||||||</div>
                        <div>{{
                        new Date(
                            +new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1) + ((i - 1) * 24 * 60 * 60 * 1000)
                        ).toLocaleString('en-US', {weekday: 'short'}) 
                        }}</div>                        
                    </div>
                </div>
            </div>
            <div class="moon__data">
                <div class="moon__data-illumination moon-data">
                    <div class="moon-data__title">
                        Illumination: 
                    </div>
                    <div class="moon-data__value">
                        {{ getIllumination }} %
                    </div>
                </div>
                <div class="moon__data-moonrise moon-data">
                    <div class="moon-data__title">
                        Moonrise: 
                    </div>
                    <div class="moon-data__value">
                        {{ dataForChart.moonrise.slice(0,5) }}
                    </div>
                </div>
                <div class="moon__data-moonset moon-data">
                    <div class="moon-data__title">
                        Moonset: 
                    </div>
                    <div class="moon-data__value ">
                        {{ dataForChart.moonset.slice(0,5) }}
                    </div>
                </div>
                <div class="moon-data__nextMoon moon-data">
                    <div class="moon-data__title">
                        Next moon:
                    </div>
                    <div class="moon-data__value">
                        through {{ getNextMoon }} days
                    </div>     
                </div>   
            </div>
            <div class="moon-modal__menu">
                <div class="modal__menu-description" >
                    <ModalMenuDescription 
                    v-for="(menu, index) in menuDescription"
                    :key="index"
                    :title="menu.title"
                    :body="menu.body"/>
                </div>
            </div>
        </div>
</template>

<script setup>
import useGetMoonValues from '@/hooks/useGetMoonValues'
import ModalMenuDescription from '@/components/modalView/ModalMenuDescription.vue'
import {toRefs } from 'vue'

const props = defineProps({
    dataForChart: {
        type: Object,
        required: true
    }
})

const {dataForChart} = toRefs(props) 

const {rotateMoon, counter, dateUnderScroll, scrollMoon, getDateForMoon, getNextMoon, getIllumination, compDateUnderScroll} = useGetMoonValues(dataForChart)

const menuDescription = [
    {title: 'About illumination', body: ''},
    {title: 'About the distance to the moon', body: ''},
]

</script>

<style scoped>
.modal__moon {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 5em;
}

.moon__block {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}


.moon__scroll {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    position: relative;
    z-index: 1;
}
.scroll-block {
    width: 80%;
    display: flex;
    overflow-x: scroll;
    scroll-snap-type: x proximity;
    padding: 0 40%;
}

.scroll__block-day {
    color: #b0b0b0;
    font-weight: normal;
}

.moon__scroll::after {
    content: '';
    position: absolute;
    border: 10px solid transparent; 
    border-top: 15px solid white;
    top: 25%;
}

.scroll__zabor {
    color: #b0b0b0;
    display: flex;
}

.scroll__zabor {
    display: flex;
    flex-direction: column;
}
.zabor {
    border-right: 2px solid white;
    padding: 0 5px;
}


.scroll__zabor:first-child  > .zabor:first-child {
    border-left: 2px solid white;

}


.moon__block img {
    width: 100%;
    height: auto;

    animation: rotate 1s linear infinite;
    animation-play-state: paused;
    animation-delay: var(--scroll);
    animation-iteration-count: 1;
    animation-fill-mode: both;
    }

@keyframes rotate {
    0% {
    transform: rotate(-45deg)
    }
    14.28% {
        transform: rotate(45deg)
    }
    28.56% {
        transform: rotate(-45deg)
    }
    42.84% {
        transform: rotate(45deg)
    }
    57.12% {
        transform: rotate(-45deg)
    }
    71.4% {
        transform: rotate(45deg)
    }
    85.68% {
        transform: rotate(-45deg)
    }
    100% {
        transform: rotate(45deg)
    }
}

.moon__block .prop {
    background-color: #1c1c1ed1;
    position: absolute;
    z-index: 1;
    border-radius: 100%;
    transform-origin: 50% top;
    box-shadow: 
        0px 20px 20px 0px #1c1c1ed1,
        0px -20px 20px 0px #1c1c1ed1;
    border: none;

    animation: rotateShadow 1s linear infinite;
    animation-play-state: paused;
    animation-delay: var(--scroll);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}


@keyframes rotateShadow {
    0% {
        transform: rotateZ(-145deg)
    }
    28.57% {
        transform: rotateZ(215deg)
    }
    57.14% {
        transform: rotateZ(575deg)
    }
    85.71% {
        transform: rotateZ(935deg)
    }
    100% {
        transform: rotateZ(1295deg)
    }
}

.moon__data {
    display: flex;
    flex-direction: column;
    padding: 10px 0;
    margin-top: 20px;
    border-radius: 10px;
    border: 1px solid #29292b;
    width: 90%;
    align-self: center;
    margin-bottom: 10px;

}
.moon-data {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #29292b;
}

.moon-data:first-child {
    padding-top: 0;
}

.moon-data:last-child {
    padding-bottom: 0;
    border-bottom: 0px solid #29292b;
}

.moon-data__title {
    font-weight: bolder;
}
.moon-data__value {
    color: #b0b0b0;
    font-weight: normal;
}

.moon-modal__menu {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: center;
}

.modal__menu-description {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

@media all and (max-width: 1920px) {
    .moon__block .prop {
        height: calc(90vw - 45vw);
        width: calc(90vw - 45vw);
    }
}

@media all and (max-width: 1024px) {
    .moon__block .prop {
        height: 90vw;
        width: 90vw;
    }
}

</style>