<template>
    <div class="app__show" v-if="weatherLoad">
        <div class="firstSticky">
            <div class="main__address">{{ weatherProps.address }}</div>
            <div class="visible__description" :style="{opacity: -opacity - 0.4}">{{ Math.round(today.temp)}}&deg; | {{ today.conditions }}</div>
        </div>
        <div class="main">
            <div class="main-description" >
                <div class="description__temperature" :style="{opacity: opacity + 0.5}">
                    {{ Math.round(today.temp)}}&deg;
                </div>
                <div class="description__conditions" :style="{opacity: opacity + 0.25}">
                    {{ today.conditions }}
                </div>
                <div class="description__temp-min-max" :style="{opacity: opacity}">
                    Max: {{ Math.round(today.tempmax) }}&deg;,
                    min: {{ Math.round(today.tempmin) }}&deg;
                </div>
            </div>
        </div>
        <div class="container">
            <ScrollHourMenu @click="openModal('Weather', 'cloud')" :opacity="opacity" :today="today" :tomorrow="weathers[1]"/>
            <TenDayForecast @check="openModal" :weathers="weathers" />
            <Map />
            <div class="additional-wrapper">
                <UvIndex :today="today.hours[hour]" @click="openModal('UV-index', 'sun')" />
                <Sunset :today="today" :tomorrow="weathers[1]" />
            </div>
            <Wind :today="today.hours[hour]" @click="openModal('Wind', 'wind')" />
            <div class="additional-wrapper">
                <FeelsLike :today="today.hours[hour]" @click="openModal('Feels like', 'temperature-three-quarters')" />
                <Precipitation :today="today" :tomorrowPrecip="weathers[1].preciptype"
                    @click="openModal('Precipitation', 'droplet')" />
            </div>
            <div class="additional-wrapper">
                <Visibility :today="today.hours[hour]" @click="openModal('Visibility', 'eye')" />
                <Humidity :today="today.hours[hour]" @click="openModal('Humidity', 'water')" />
            </div>
            <Moon :hour="hour" :tomorrow="weathers[1]" :today="today" @click="openModal('Moon', 'moon')" :dataForChart="dataForChart"/>
            <div class="additional-wrapper">
                <Average :today="today" />
                <Pressure :hour="hour" :today="today" @click="openModal('Pressure', 'gauge')" />
            </div>
        </div>
        
            <!-- <div class="app__bottom">
                <div class="bottom__wrapper">
                    <div class="bottom__elem bottom__map">
                        <i class="fa-regular fa-map"></i>
                    </div>
                    <div class="bottom__elem bottom__page">
                        <div class="page__elem" v-for="i in 2" @click="console.log(i)">
                            <i v-if="true"  class="fa-solid fa-circle"></i>
                            <i v-else class="fa-regular fa-circle"></i>
                        </div>
                    </div>
                    <div class="bottom__elem bottom__modal" >
                        <i class="fa-solid fa-bars"></i>
                    </div>
                </div>
            </div> -->
            <MyModal :today="today" :titleModal="titleModal" :weathers="weathers" :icon="icon"
            :hour="hour" :selectDate="selectDate || today.datetime" @closeModal="closeModal"
            @changeOptions="changeOptions" @changeDate="changeDate" class="outer" :style="{maxHeight: selectedModal ? '90vh' : null, minHeight: selectedModal ? '90vh' : null}"/>
    </div>
</template>

<script setup>
import ScrollHourMenu from '@/components/basicView/ScrollHourMenu.vue'
import TenDayForecast from '@/components/basicView/TenDayForecast.vue'
import UvIndex from '@/components/basicView/UvIndex.vue'
import Wind from '@/components/basicView/Wind.vue'
import Precipitation from '@/components/basicView/Precipitation.vue'
import FeelsLike from '@/components/basicView/FeelsLike.vue'
import Visibility from '@/components/basicView/Visibility.vue'
import Humidity from '@/components/basicView/Humidity.vue'
import Moon from '@/components/basicView/Moon.vue'
import Pressure from '@/components/basicView/Pressure.vue'
import Average from '@/components/basicView/Average.vue'
import useFetchWeather from '@/hooks/useFetchWeather'
import useOCModal from '@/hooks/useOCModal'
import MyModal from '@/components/modalView/MyModal.vue'
import Sunset from '@/components/basicView/Sunset.vue'
import Map from './Map.vue'

import { computed, onMounted, ref, watch } from 'vue'


const { weather } = defineProps({
    weather: {
        type: Object,
        required: true
    }
})

const { weatherProps, weathers, weatherLoad, today } = useFetchWeather(weather)
const { openModal, closeModal, changeOptions, changeDate, selectedModal, titleModal, icon, selectDate, yBody } = useOCModal()
const hour = ref(new Date().getHours())


const dataForChart = computed(() => {
    return weathers.value[Math.abs(new Date(today.value.datetime).getDate() - new Date().getDate())]
})

const opacity = ref(1)

document.addEventListener('scroll', (event) => {
    const scrollPosition = window.scrollY;
    opacity.value = 1 - scrollPosition / 70
})

</script>

<style scoped>

* {
    color: white;
    transition: all 0.4s linear;
    scroll-behavior: smooth;
}


.container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 50px 20px 0;
}

.additional-wrapper {
    display: flex;
    gap: 10px;
}

.app__show {
    padding-bottom: 100px;
}

.firstSticky {
    position: sticky;
    top: 0;
    height: fit-content;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px 0 0;
    padding: 20px 0;
    backdrop-filter: blur(10px);
    background: rgb(121,232,241);
background: -moz-linear-gradient(90deg, rgba(121,232,241,1) 3%, rgba(34,83,204,1) 100%);
background: -webkit-linear-gradient(90deg, rgba(121,232,241,1) 3%, rgba(34,83,204,1) 100%);
background: linear-gradient(90deg, rgba(121,232,241,1) 3%, rgba(34,83,204,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#79e8f1",endColorstr="#2253cc",GradientType=1);
}


.main {

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.main__address {
    font-size: 2em;
    font-weight: normal;
}

.main-description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.description__temperature {
    font-size: 6.5em;
    font-weight: lighter;
    max-height: 150px;
}
.description__conditions {
    font-weight: normal;
    max-height: 50px;
    font-size: 1.2em;

}
.description__temp-min-max {
    font-weight: normal;
    max-height: 50px;
    font-size: 1.2em;
}

.visible__description {
    font-weight: normal;
    font-size: 1.2em;
    max-height: 0px;
    overflow: hidden;
    max-height: fit-content;
}



.my-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.outer {
    max-height: 0vh;
    min-height: 0vh;
    transition: all 0.4s ease-in-out;
    position: fixed;
    bottom: 0;
    z-index: 100;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background: #1c1c1e; 
    background: #55556d;
}

@media all and (max-width: 1920px) {
    .outer {
        width: 50vw;
        margin: 0 auto;
    }
}

@media all and (max-width: 1024px) {    
    .outer  {
        width: 100%;
    }
}
</style>