import axios from "axios"
import { onMounted, ref } from "vue"

export default function useWeather(location) {
    const weatherCollection = ref({})
    const collectLoading = ref(false)

    const fetching = async () => {     
        try {
            const response = await axios.get(`https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timelinemulti?unitGroup=metric&key=GKSU7U2B6QAULYPX2FYURQNAW&include=hours&elements=datetime,moonset,moonrise%2CdatetimeEpoch%2Cname%2Caddress%2CresolvedAddress%2Clatitude%2Clongitude%2Ctempmax%2Ctempmin%2Ctemp%2Cfeelslikemax%2Cfeelslikemin%2Cfeelslike%2Cdew%2Chumidity%2Cprecip%2Cprecipprob%2Cprecipcover%2Cpreciptype%2Csnow%2Csnowdepth%2Cwindgust%2Cwindspeed%2Cwindspeedmax%2Cwindspeedmean%2Cwindspeedmin%2Cwinddir%2Cpressure%2Ccloudcover%2Cvisibility%2Csolarradiation%2Csolarenergy%2Cuvindex%2Csevererisk%2Csunrise%2Csunset%2Cmoonphase%2Cconditions%2Cdescription%2Cicon%2Csource`, {
                params: {
                    locations: location
                }
            });
            
            weatherCollection.value = response.data
            collectLoading.value = true
        } catch (error) {
            console.log(error)
        }
    }

    onMounted(fetching)

    return {
        weatherCollection,
        collectLoading
    }
}