import {computed, ref, watch, onBeforeMount} from 'vue' 
export default function useRenderChartData(visibilityDesc, uvIndex, dataForChart, chart, hour) {
    const datetime = ref('')
    const chartProperty = ref('')
    const chartUnit = ref('')
    const descriptionUV = ref('')
    const descriptionVisib = ref('')
    const hourValue = ref({})

    const getRenderData = () => {
        datetime.value = dataForChart.value.datetime
        hourValue.value = dataForChart.value.hours[hour.value]
        chartProperty.value = chart.value.prop
        chartUnit.value = chart.value.unit
        visibilityDesc.find(item => item.km > hourValue.value.visibility ? descriptionVisib.value = item.name: false)
        uvIndex.find(item => item.value > dataForChart.value.uvindex ? descriptionUV.value = item.name : null)
    }

    const titleValue = computed(() => {
        return new Date().getDate() === new Date(datetime.value).getDate() ?
               Math.round(hourValue.value[chartProperty.value])  + ' ' + chartUnit.value :
               Math.round(dataForChart.value[chartProperty.value])  + ' ' + chartUnit.value 
    })

    const subtitleValue = computed(() => {
        return new Date().getDate() === new Date(datetime.value).getDate() ? hourValue.value : dataForChart.value
    })


    onBeforeMount(getRenderData)
    watch(dataForChart, getRenderData)
    watch(chart, getRenderData)


    return {
        titleValue, subtitleValue,
        datetime, chartProperty, chartUnit, hourValue,
        descriptionVisib, descriptionUV
    }
}