            <div class="precipitation__descrip-value">in the last 24 hours</div>
            <div class="precipitation__description">{{ tomorrowPrecip ? 'Expected tomorrow ' + tomorrowPrecip.join(', ') : 'No precipitation tomorrow' }}</div>
        </Wrapper>
    </Container>
</template>

<script setup>

const {today, tomorrowPrecip} = defineProps({
    today: {
        type: Object,
        required: true
    },
    tomorrowPrecip: {
        type: [Object, null],
        required: true
    }
})
</script>

<style scoped>
.precipitation__value {
    font-size: 2em;
