<template>
    <Container>
        <VTitle :side="'right'">
            <template #icon="{style}">
                <i class="fa-solid fa-gauge" :style="style"></i>
            </template>
            <template #text>
                pressure
            </template>
        </VTitle>
        <Wrapper class="pressure">
            <div class="pressure__title">
                <div class="pressure__arrow-for-value">
                    <i class="fa-solid fa-arrow-up" v-if="val > prev"></i>
                    <i class="fa-solid fa-arrow-down" v-else-if="val < prev"></i>
                    <i class="fa-solid fa-equals" v-else></i>
                </div>
                <div class="pressure__value">{{ Math.round(val)}}</div>
                <div class="pressure__description">hPa</div>
                <div class="cursor" :style="{transform: `rotate(${degress}deg) translateX(-50%)`}">|</div>
            </div>
            <div class="pressure__arrows">
                <i class="fa-solid fa-arrow-down" ></i>
                <i class="fa-solid fa-arrow-up"></i>
            </div>
        </Wrapper>
    </Container>
</template>

<script setup>
import { computed, ref } from 'vue';

const {today, hour} = defineProps({
    today: {
        type: Object,
        required: true
    },
    hour: {
        type: Number,
        default: new Date().getHours()
    }
})




const prev = ref(0)
const val = ref(0)
prev.value = today.hours[hour ? hour - 1 : hour].pressure
val.value = today.hours[hour].pressure

const degress = computed(() => {
    // ((value - minValue)/(maxValue - minValue) * 100%) / 100% * (maxDegress - minDegress) + minDegress
    return ((val.value - 900)/(1200 - 900) * 100) / 100 * (120 + 120) -120
})

</script>

<style scoped>
.pressure {
      flex: 1 0 0;
}
.pressure__title {
    position: relative;
    flex: 1 0 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pressure__title::before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.4;
    background: url('../../asserts//img/timer.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.cursor {
    position: absolute;
    top: -10%;
    left: 50%;
    height: 75%;
    font-size: 2em;
    font-weight: bold;
    display: flex;
    justify-content: center;
    text-shadow: 0 0 15px #fff;
    transform-origin: bottom;
}
.pressure__arrow-for-value {
    margin-top: 20px;
}
.pressure__value {
    font-weight: normal;
    font-size: 1.5em;
}
.pressure__description {
}
.circle__pressure {

}

.pressure__arrows {
    display: flex;
    justify-content: center;
    gap: 4em;
    padding: 10px 0;
}

</style>