import { ref } from "vue"
export default function useSun(concatHourTwoDays) {
    const sunRiseSet = ref([])
    const sun = ref({
        stateOne: {},
        stateTwo: {}
    })
    
    sunRiseSet.value = concatHourTwoDays.filter(i => typeof i === 'string')
    
    if (sunRiseSet.value[0].slice(0,2) > 12) {
        sun.value.stateOne.state = 'sunset'
        sun.value.stateTwo.state = 'sunrise'
    } else {
        sun.value.stateOne.state = 'sunrise'
        sun.value.stateTwo.state = 'sunset'
    }

    return {
        sunRiseSet,
        sun
    }
}