export default {
    mounted(el, binding) {
        const options = {
            rootMargin: '0px',
            threshold: [0, 1]
        }
        let callback = (entries, observer) => {
            console.log(entries[0])
            console.log(entries[0].boundingClientRect.y)
            // if (entries[0].boundingClientRect.y > 0) {
            //     binding.value()
            //   }
        }
        let observer = new IntersectionObserver(callback, options);
        observer.observe(el)
    },
    name: 'intersection'
}