import { ref } from "vue"

export default function useConcatHourTwoDays(today, tomorrow) {
    const nowHour = new Date().getHours()

    const collectHours = (day) => {
        const sunrise = day.sunrise
        const sunset = day.sunset
        const hours = [...day.hours]

        hours.splice(+sunrise[1] + 1, 0, sunrise)
        hours.splice(+sunset.slice(0,2) + 2, 0, sunset)
        return hours
    }

    const todayHours = collectHours(today)
    const tomorrowHours = collectHours(tomorrow)
    
    const concatHourTwoDays = ref([...todayHours, ...tomorrowHours].slice(nowHour + 1, nowHour + 28))
    return {
        concatHourTwoDays
    }
}