<template>
    <div class="specification__title">
        <div class="title">
            {{ titleValue }}
            <span v-if="chart.configName === 'uvindex'">{{ descriptionUV }}</span>  
            <i class="fa-solid" :class="'fa-' + icon"></i>
        </div>
        <div class="specification__subtitle">
            <span v-if="chart.configName === 'weather'">
                <i class="fa-solid fa-arrow-down"></i>
                {{ Math.round(dataForChart.tempmin) + '' + chartUnit}}
                <i class="fa-solid fa-arrow-up"></i>
                {{ Math.round(dataForChart.tempmax) + '' + chartUnit}}
            </span>
            <span v-else-if="chart.configName === 'visibility'">
                {{ descriptionVisib }}
            </span>
            <span v-else-if="chart.configName === 'feelslike'">
                Actually:  
                max: {{ Math.round(dataForChart.tempmax) + '' + chartUnit}}
                min: {{ Math.round(dataForChart.tempmin) + '' + chartUnit}}
            </span>
            <span v-else-if="chart.configName === 'wind'">
                Gusts: {{ Math.round(subtitleValue.windgust) + ' ' + chartUnit}}
            </span>
            <span v-else-if="chart.configName === 'humidity'">
                Dew: {{ Math.round(subtitleValue.dew)  + ' ' + chartUnit}}
            </span>
            <span v-else-if="chart.configName === 'uvindex'">      
                UFI World Health Organization
            </span>
            <span v-else-if="chart.configName === 'pressure'">
                {{subtitlePressure}}
            </span>
            <span v-else-if="chart.configName === 'precipitation'">
                General data for the day
            </span>
        </div>
    </div>
</template>

<script setup>
import useVisibility from '@/hooks/useVisibility'
import useUVindex from '@/hooks/useUVindex'
import useRenderChartData from '@/hooks/useRenderChartData'
import { computed, onMounted, ref, toRefs } from 'vue'

const props = defineProps({
    dataForChart: {
        type: Object,
        required: true
    },
    chart: {
        type: Object,
        required: true
    },
    hour: {
        type: Number,
        default: new Date().getHours()
    },
    icon: {
        type: String,
        required: true
    }
})

const {dataForChart, chart, hour, icon} = toRefs(props)

const pressureData = ref([])

onMounted(() => {
    pressureData.value = dataForChart.value.hours.map(hour => hour.pressure)
})

const subtitlePressure = computed(() => {    
    let Falls = true
    let Growing = true

    pressureData.value.forEach((item, index) => {
        if(index === 0) return
        item - pressureData.value[index - 1] < 0 ? Growing = false : Falls = false
    })

    return Falls ? 'Falls' : Growing ? 'Growing' : 'Average'
})
            
const {visibilityDesc, descr} = useVisibility(dataForChart.value)
const {uvIndex, uvSubtitle} = useUVindex(dataForChart.value)

const {titleValue, subtitleValue, datetime, chartProperty, chartUnit, hourValue, descriptionVisib, descriptionUV} = useRenderChartData(visibilityDesc, uvIndex, dataForChart, chart, hour)

</script>

<style scoped>
.specification__title {
    padding-bottom: 10px;
}

.title {
    font-size: 2em;
    font-weight: normal;
    display: flex;
    align-items: center;
    gap: 10px;
}

.title span {
    font-size: inherit;
    font-weight: inherit;
}

.title i{
    font-size: 0.8em;
}
.specification__subtitle span{
    font-weight: normal;
    font-size: 1em;
    display: flex;
    align-items: center;
    gap: 5px;
    color: #b0b0b0;
}

.specification__subtitle span i {
    font-size: 0.8em;
    color: #b0b0b0;
}
</style>