import { ref, watch } from "vue" 
export default function useVisibility(today) {
    const descr = ref('')
    const visibilityDesc = [
        {km: 0.5, name: 'Very poor visibility'},
        {km: 2, name: 'bad visibility'},
        {km: 10, name: 'Average visibility'},
        {km: 20, name: 'Good visibility'},
        {km: 50, name: 'Very good visibility'}, 
    ]

    visibilityDesc.find(item => item.km > today.visibility ? descr.value = item.name: false)

    return {
        visibilityDesc,
        descr
    }
}