<template>
    <Container >
        <VTitle :side="'right'">
            <template #icon="{style}">
                <i class="fa-solid fa-circle-chevron-up" v-if="true" :style="style"></i>
                <i class="fa-solid fa-circle-chevron-down" v-else :style="style"></i>
            </template>
            <template #text>
                {{ sun.stateOne.state }}
            </template>
        </VTitle>
        <Wrapper class="sun">
            <div class="sun__time">
                {{ sunRiseSet[0].slice(0, 5) }}
            </div>
            <div class="sun__diagramm"></div>
            <div class="sun__subtitle">
                {{ sun.stateTwo.state }}
                {{ sunRiseSet[1].slice(0, 5) }}
            </div>
        </Wrapper>
    </Container>
</template>

<script setup>
import useConcatHourTwoDays from '@/hooks/useConcatHourTwoDays';
import useSun from '@/hooks/useSun';


const {today, tomorrow} = defineProps({
    today: {
        type: Object,
        required: true
    },
    tomorrow: {
        type: Object,
        required: true
    }
})

const { concatHourTwoDays } = useConcatHourTwoDays(today, tomorrow);
const { sunRiseSet, sun } = useSun(concatHourTwoDays.value);

</script>

<style scoped>
.sun {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   flex: 1 0 0;
}
 

.sun__time {
    font-size: 2em;
    font-weight: normal;
}
.sun__diagramm {
}
.sun__subtitle {
}
</style>