import { ref } from "vue";

export default function useMoon(today, tomorrow, hour) {
    const moon = ref({})

    const todayMoonphase = today.moonphase
    const todayMoonset = today.moonset
    const todayMoonrise = today.moonrise
    const tomorrowMoonset = tomorrow.moonset

    if (todayMoonrise && tomorrowMoonset) {
        if (todayMoonset && hour < +(todayMoonset.slice(0,2))) {
            moon.value.state = 'moonset'
            moon.value.time = todayMoonset.slice(0, 5)
        } else if (hour < +(todayMoonrise.slice(0,2))) {
            moon.value.state = 'moonrise'
            moon.value.time = todayMoonrise.slice(0, 5)
        } else {
            moon.value.state = 'moonset'
            moon.value.time = tomorrowMoonset.slice(0, 5)
        }
    }

    const Illumination = (todayMoonphase > 0.5 ? Math.abs(todayMoonphase - 1) / 0.5 : todayMoonphase / 0.5) * 100
    
    const nextMoon = todayMoonphase * 29.5 > 30 ? Math.abs(Math.round(todayMoonphase * 29.5) - 30) + 15 : Math.abs(Math.round(todayMoonphase * 29.5)) + 15 - 1

    return {
        Illumination,
        nextMoon,
        moon
    }
}