<template>
    <div class="wrapper-title" :style="{top: heightTitle - 1 + 'px', ...sideBG}">
        <h2 class="title">
            <slot name="icon" :style="icon"></slot>
            <slot name="text"></slot>
        </h2>
    </div>
</template>

<script>
 export default {
    name: 'VTitle'
 }
</script>

<script setup>
import { computed, onMounted, ref, watch } from 'vue';
const {side} = defineProps({
    side: String
})

const sideBG = computed(() => {
    if (side && side === 'left') {
        return {
            background: 'linear-gradient(90deg, rgba(121,232,241,1) calc(3% - 20px), rgba(34,83,204,1) calc(215% + 20px))'

        }
    }
    else if (side && side === 'right') {
        return {
            background: 'linear-gradient(90deg, rgba(121,232,241,1) calc(-97% - 20px), rgba(34,83,204,1) calc(100% + 20px))'    
        }
    }
    else null
})


const icon = {
    color: '#d3cccc85',
    fontSize: '0.9em',
}

const heightTitle = ref(0)

    
document.addEventListener('scroll', (event) => {
    heightTitle.value = document.querySelector('.firstSticky')?.offsetHeight
})

</script>

<style scoped>

.wrapper-title {
    background: rgb(121,232,241);
    background: -moz-linear-gradient(90deg, rgba(121,232,241,1) calc(3% - 20px), rgba(34,83,204,1) calc(100% + 20px));
    background: -webkit-linear-gradient(90deg, rgba(121,232,241,1) calc(3% - 20px), rgba(34,83,204,1) calc(100% + 20px));
    background: linear-gradient(90deg, rgba(121,232,241,1) calc(3% - 20px), rgba(34,83,204,1) calc(100% + 20px));
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#79e8f1",endColorstr="#2253cc",GradientType=1);
    position: sticky;
    z-index: 100;
    top: 0;
    height: fit-content;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    
}
.title {
    display: flex;
    gap: 5px;
    padding: 10px 10px 5px;
    align-items: center;
    border-radius: 8px;
    background: linear-gradient(90deg , rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%);
    color: #d3cccc85;
    font-size: 0.9em;
    text-transform: uppercase;
    font-weight: normal;
}

</style>